
import Formula from "@/components/Formula.vue";
import { Options, Vue } from "vue-class-component";

@Options({
  props: {
    yy: Number,
    cd: Number,
  },
  components: {
    Formula,
  },
})
export default class YearDoomsday extends Vue {
  yy!: number;
  cd!: number;

  floor(value: number): number {
    return Math.floor(value);
  }
}
